
.main-wrapper {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
.dialog__container {
  display: inline-block;
  margin-top: 25px;
  margin-left: 10px;
  width: 125px;
}
#bar {
  position: absolute;
  z-index: 1;
  left: 10px;
  top: 20px;
  left: 10px;
  width: 410px;
  height: 60px;
}
#bar label {
  color: grey;
}
body {
  overflow: hidden;
}
.mr-3 {
  margin-top: 15px;
}
.picker__body {
  background-color: white;
  color: black;
}
.card__actions {
  background-color: white;
}
input[type=text] {
  margin-top: 20px;
  font-size: 16px;
  width: 200px;
}




